import {React, Component} from 'react'


class Messages extends Component {
    // constructor(props) {
    //     super(props)
    // }

    render() {
        return(
            <div className="mainApp-inner-activePage-messages">
                <div className="messages messages-outer">
                    <div className="messages-inner">
                        <div className="messages-inner-top">
                            <div className="messages-inner-top-left">
                                <ul className="messages-inner-top-left-wrapper">
                                    <li className="messages-inner-top-left-wrapper-student">
                                        სტუდენტი
                                    </li>

                                    <li className="messages-inner-top-left-wrapper-professor">
                                        პროფესორი
                                    </li>

                                    <li className="messages-inner-top-left-wrapper-administration">
                                        ადმინისტრაცია
                                    </li>
                                </ul>
                            </div>

                            <div className="messages-inner-top-right">
                                <div className="messages-inner-top-right-newMsg">
                                    <button className="messages-inner-top-right-newMsg-btn">
                                        + ახალი შეტყობინება
                                    </button>
                                </div>
                            </div>
                        </div>

                        <div className="messages-inner-main">
                            <div className="messages-inner-main-search">
                                <div className="messages-inner-main-search-inner">
                                    <input type="text" id="msgSearch" className="messages-inner-main-search-inner-input" placeholder="ძებნა..."/>
                                </div>
                            </div>

                            <div className="messages-inner-main-chat messages-inner-main-chat-outer">
                                <div className="messages-inner-main-chat-inner">
                                    <div className="messages-inner-main-chat-inner-field">
                                        შეტყობინება არ არის
                                    </div>
                                    <div className="messages-inner-main-chat-inner-msgArea">
                                        <div className="messages-inner-main-chat-inner-msgArea-textArea">
                                            <textarea placeholder="შეტყობინება..."></textarea>
                                        </div>

                                        <div className="messages-inner-main-chat-inner-msgArea-footer">
                                            <div className="messages-inner-main-chat-inner-msgArea-footer-uploadFile">
                                                <label className="messages-inner-main-chat-inner-msgArea-footer-uploadFile-label">
                                                    <input type="file" style={{display: 'none'}} id="msgFileUpload"/>
                                                    ფაილის მიმაგრება
                                                </label>
                                            </div>

                                            <div className="messages-inner-main-chat-inner-msgArea-footer-send">
                                                <button className="messages-inner-main-chat-inner-msgArea-footer-send-btn">
                                                    გაგზავნა
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
            
                    </div>
                </div>
            </div>
        )
    }
}

export default Messages;
